// @ts-check
import React from 'react';
import { StaticQuery, graphql, Link } from 'gatsby';
import typeStyles from '../components/typography.module.css';
import homeStyles from '../components/home.module.css';
import hljs from 'highlight.js';
import Helmet from 'react-helmet';
import logo from '../components/xstate-logo-white.svg';

class Home extends React.Component {
  render() {
    const { children } = this.props;

    return (
      <StaticQuery
        query={graphql`
          query HomeSiteTitleQuery {
            site {
              siteMetadata {
                title
              }
            }
          }
        `}
        render={data => (
          <main className={homeStyles.main}>
            <Helmet
              title={data.site.siteMetadata.title}
              meta={[
                {
                  name: 'description',
                  content:
                    'XState is a library for creating state machines and statecharts in JavaScript.'
                },
                { name: 'keywords', content: 'sample, something' }
              ]}
            >
              <html lang="en" />
              <link
                href="https://fonts.googleapis.com/css?family=Roboto:300,700|Source+Code+Pro"
                rel="stylesheet"
              />
            </Helmet>

            <a
              href="https://github.com/davidkpiano/xstate"
              className={homeStyles.logo}
            >
              <img src={logo} alt="XState" />
            </a>
            <ul className={homeStyles.links}>
              <li className={homeStyles.link} style={{ '--i': 0 }}>
                <a href="https://github.com/davidkpiano/xstate">GitHub</a>
              </li>
              <li className={homeStyles.link} style={{ '--i': 1 }}>
                <Link to="guides/start">Docs</Link>
              </li>
              <li className={homeStyles.link} style={{ '--i': 2 }}>
                <a href="https://spectrum.chat/statecharts">Community</a>
              </li>
            </ul>
          </main>
        )}
      />
    );
  }
}

export default Home;
